<template>
  <div class="hello">
  	<div v-if="active == 1">
  		<div>
  			向 以 各种姿势划水的程序猿 致敬
  		</div>
  		<img src="../../static/dog.gif"/>
  	</div>
  	<div v-if="active == 2">
  		222
  		<!--<audio src="../../static/哈你有在.mp3" >音乐</audio>-->
  		<!--<audio  src="http://win.web.rh01.sycdn.kuwo.cn/3694085b23f3373731fd23da10707840/5d3024bf/resource/n2/65/12/519855511.mp3"></audio>-->
  		
  		<audio ref="audio" class="audio" controls="controls" preload  >
			  <source src="../../static/hhh.mp3" type="audio/ogg">
			</audio>
  		
  		<!--<audio ref="audio" class="dn" 
		    :src="url" :preload="audio.preload"
		    @play="onPlay" 
		    @error="onError"
		    @waiting="onWaiting"
		    @pause="onPause" 
		    @timeupdate="onTimeupdate"
		    @loadedmetadata="onLoadedmetadata"
	    
	    ></audio>-->
  		
  		<div v-if="dis" class="jingxi" @click="startPlay">
  			惊喜
  		</div>
  		
  		<img v-if="!dis" @click="disNone" src="../../static/bug.jpg"/>
  	</div>
    <div class="btm">
    	<div class="l" @click="toyemian(1)">首页</div>
    	<div class="r" @click="toyemian(2)">哈哈哈</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      active:1,
      dis:true
    }
  },
  methods:{
  	toyemian(e){
  		this.active = e
  	},
  	startPlay() {
	    this.$refs.audio.play()
	    this.dis = false
	  },
	  disNone(){
	  	this.dis = true
	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.audio{
		opacity: 0;
		position: absolute;
		left: 0;
		top: 0;
	}
	.btm{
		display: flex;
		justify-content: space-around;
		align-items: center;
		height: 30px;
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
	}
	.btm div{
		width: 50%;
		color: #fff;
		height: 30px;
		line-height: 30px;
	}
	.btm div.l{
		background-color: #2C3E50;
	}
	.btm div.r{
		background-color: #1e1;
	}
	.jingxi{
		display: inline-block;
		padding: 10px;
		background-color: #11EE11;
		color: #fff;
		position:absolute;
		animation:mymove 5s infinite;
	}
	@keyframes mymove
		{
		0% {top:0%;left: 0%;background-color: #FF1493;}
		5% {top:10%;left: 20%;background-color: #FF1493;}
		10% {top:5%;left: 4%;background-color: #800080;}
		15% {top:43%;left: 60%;background-color: #9932CC;}
		20% {top:29%;left: 10%;background-color: #8A2BE2;}
		25% {top:87%;left: 52%;background-color: #B0C4DE;}
		30% {top:78;left: 10%;background-color: #708090;}
		35% {top:52%;left: 20%;background-color: #4682B4;}
		40% {top:35%;left: 30%;background-color: #B0E0E6;}
		40% {top:97%;left: 50%;background-color: #00FFFF;}
		40% {top:71%;left: 70%;background-color: #008B8B;}
		40% {top:85%;left: 50%;background-color: #7FFFAA;}
		40% {top:97%;left: 80%;background-color: #3CB371;}
		40% {top:71%;left: 20%;background-color: #F0E68C;}
		40% {top:22%;left: 40%;background-color: #FFA500;}
		40% {top:65%;left: 10%;background-color: #FF4500;}
		40% {top:96%;left: 50%;background-color: #B22222;}
		40% {top:58%;left: 65%;background-color: #FAFAD2;}
		40% {top:83%;left: 70%;background-color: #40E0D0;}
		40% {top:74%;left: 60%;background-color: #00BFFF;}
		100%{top:0%;left: 0%;background-color: #FF1493;}
		}
</style>



